import { TreeStructureResponse } from '@/interface/treeStructureResponse';

export function findChildrenByIDOrName(
    node: TreeStructureResponse | null,
    idOrName: string,
): TreeStructureResponse | null {
    if (node && (node.id === idOrName || node.name === idOrName)) {
        return node;
    } else if (node && node.children) {
        let result: TreeStructureResponse | null = null;
        for (let i = 0; result === null && i < node.children.length; i++) {
            result = findChildrenByIDOrName(node.children[i], idOrName);
        }
        return result;
    }
    return null;
}
